/* 固定页面，不允许滚动 */
html, body, .overview-page {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
}

/* 整个页面固定为视口高度 */
.overview-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

/* 主内容区域，允许子项正确收缩 */
.overview-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;
}

/* 上半部分：视频区域，由 flex 严格控制比例 */
.top-section {
  flex: 8;  /* 可根据需要调整比例 */
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f5f5f5;
  overflow: hidden;
}

/* 下半部分：三个板块，添加内边距和面板间距 */
.bottom-section {
  flex: 2;  /* 可根据需要调整比例 */
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #eaeaea;
  padding: 20px;
  gap: 20px;
}

/* 公共正方形板块样式：保持正方形，添加边框、圆角和间距 */
.square-panel {
  flex: 1;           /* 平均分配空间 */
  max-width: 25%;    /* 每个面板最大宽度 */
  aspect-ratio: 1/1;  /* 保持正方形 */
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid rgba(204, 204, 204, 0.6);
  border-radius: 15px;       /* 圆角效果 */
  overflow: hidden;          /* 内部内容也显示圆角 */
  background-size: cover;
  background-position: center;
  position: relative;
}

/* 内部图片自适应且带点击提示 */
.square-panel img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
  display: block;
}
